.map {
  width: 100%;
  height: 100%;
  position: fixed; }

.location-marker {
  font-size: 2rem; }

.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh; }

.loader img {
  width: 400px; }

.loader h1 {
  margin-top: -100px; }

.location-info {
  position: fixed;
  bottom: 55px;
  right: 55px;
  width: 400px;
  min-height: 200px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 18px;
  color: #fff; }

.map-buttons {
  position: fixed;
  top: 100px;
  left: 300px;
  width: 300px;
  height: 400px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 14px;
  color: #808080; }

.location-info ul {
  list-style-type: none;
  padding: 0; }

.location-info li {
  padding: 5px 0; }
